import { useRouteMatch } from "react-router-dom";
import { useStores } from "../store";
import { useCallback } from "react";
import { CookieGroupModel } from "../models";

const useRouteCookieGroup = () => {
  const {
    params: {
      cookieGroup
    }
  } = useRouteMatch();
  const {
    cookieStore
  } = useStores(); // Get cookie group or return draft object

  const id = isNaN(+cookieGroup) ? 0 : +cookieGroup;
  const queried = !!cookieGroup;
  const group = cookieStore.groups.entries.get(id) || new CookieGroupModel(cookieStore.groups, {
    id: 0
  });
  const link = "#/cookies";
  const editLink = useCallback(({
    key
  }) => "#/cookies/".concat(group.key, "/edit/").concat(key), [group]);
  const addCookieLink = "#/cookies/".concat(group.key, "/new");
  return {
    group,
    id,
    queried,
    fetched: group.key !== 0,
    link,
    editLink,
    addCookieLink
  };
};

export { useRouteCookieGroup };