import { useRouteMatch } from "react-router-dom";
import { CookieModel } from "../models";
import { useRouteCookieGroup } from ".";

const useRouteCookie = () => {
  const routeGroup = useRouteCookieGroup();
  const {
    group
  } = routeGroup;
  const {
    params
  } = useRouteMatch(); // Get cookie or return draft object

  const id = isNaN(+params.cookie) ? 0 : +params.cookie;
  const queried = !!params.cookie;
  const cookie = group.cookies.entries.get(+params.cookie) || new CookieModel(group.cookies, {
    id: 0
  });
  return {
    routeGroup,
    cookie,
    id,
    queried,
    fetched: cookie.key !== 0
  };
};

export { useRouteCookie };